import React from "react";
import Actorcard from "./Actorcard";
import { FlexGrid } from "../styled";
import { useShows } from "../../misc/custom-hook";
// import img from '../../images/img.png';

export default function Actorgrid({ data }) {
  const [starredShows, dispatchStarred] = useShows();

  return (
    <FlexGrid>
      {data.map(({ person }) => {
        const isStarred = starredShows.includes(person.id);

        const onStarClick = () => {
          if (isStarred) {
            dispatchStarred({ type: "REMOVE", showId: person.id });
            console.log("hyy");
          } else {
            dispatchStarred({ type: "ADD", showId: person.id });
          }
        };

        // const poster =
        //   "data:image/png;base64,";

        return (
          <Actorcard
            key={person.id}
            id={person.id}
            name={person.name}
            gender={person.gender}
            country={person.country ? person.country.name : null}
            birthday={person.birthday}
            img={person.image ? person.image.medium : null}
            isStarred={isStarred}
            onStarClick={onStarClick}
          />
        );
      })}

      {/* {data.map(({ person }) => (
        <Actorcard
          key={person.id}
          id={person.id}
          name={person.name}
          gender={person.gender}
          country={person.country ? person.country.name : null}
          birthday={person.birthday}
          img={person.image ? person.image.medium : null}
          isStarred={isStarred}
          onStarClick={onStarClick}
        />
      ))} */}
    </FlexGrid>
  );
}
