import React from "react";
import { Headline, MainDataWrapper } from "../show/Showmainstyle";

export default function ActorMainData(props) {
  const pro =
    "data:image/jpeg;base64,";

  return (
    <MainDataWrapper>
      <img src={props.image ? props.image.original : pro} alt={props.name} />
      <div className="text-side">
        <Headline>
          <h1>Name: {props.name}</h1>
          <h2>Gender: {props.gender ? props.gender : "Null"}</h2>
        </Headline>
        <h2>Date of Birth: {props.birthday ? props.birthday : "Null"}</h2>
        <h3>
          Country Name: {props.country ? props.country.name : "Not found"}
        </h3>
        <h3>
          Timezone: {props.country ? props.country.timezone : "Not found"}
        </h3>
      </div>
    </MainDataWrapper>
  );
}
